import React from 'react'
import { Typography } from 'antd'
import LandingLayout from '@layouts/LandingLayout'

export default () => (
  <LandingLayout activeTab = '2'>
    <div className='flex flex-col items-center'>
    <Typography.Title className='text-primary-color'>
        SecureT - personal encryption made easy
      </Typography.Title>
      <Typography.Title level={4} className='text-gray-600'>
        SecureT allows you to generate your own encryption keys and encrypt and decrypt messages with them. The app provides assurance that your messages will remain private and that your privacy is in your own hands. SecureT is coming to Android and iOS soon.
      </Typography.Title>
      <img src='/SecureT.png' alt='logo' className='mt-10' />
    </div>
  </LandingLayout>
)
